.layout {
  min-height: 100vh;
}

.logo {
  font-size: 20px;
  margin-right: 24px;
  font-weight: 600;
}

.logo a {
  color: white;
  text-decoration: none;
  display: flex;
  align-items: center;
}

.site-layout-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 24px;
}

.ant-menu-dark.ant-menu-horizontal > .ant-menu-item {
  margin: 0 4px;
  border-radius: 4px;
  transition: all 0.3s;
}

.ant-menu-dark.ant-menu-horizontal > .ant-menu-item-selected {
  background-color: rgba(255, 255, 255, 0.15);
}

.ant-menu-dark.ant-menu-horizontal > .ant-menu-item > a {
  color: rgba(255, 255, 255, 0.85);
  padding: 0 16px;
}

.ant-menu-dark.ant-menu-horizontal > .ant-menu-item-selected > a {
  color: white;
}

.ant-card-head {
  background: linear-gradient(to right, #fafafa, #f5f5f5);
  padding: 16px 24px;
  border-bottom: 1px solid #f0f0f0;
}

.ant-card-head-title {
  font-size: 18px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.85);
}

.ant-table-thead > tr > th {
  background-color: #fafafa;
  padding: 16px;
  font-weight: 600;
}

.ant-alert {
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.ant-alert-message {
  font-weight: 600;
}